import React from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import './Header.css';

function Header() {
  const location = useLocation();
  const navigate = useNavigate();

  //    
  const handleNavClick = (e, hash) => {
    e.preventDefault();
    if (location.pathname === '/') {
      const el = document.getElementById(hash);
      if (el) {
        el.scrollIntoView({ behavior: 'smooth' });
      } else {
        //     ,  hash, useEffect  Home 
        window.location.hash = hash;
      }
    } else {
      navigate('/#' + hash);
    }
  };

  return (
    <header className="header">
      <nav className="header-nav">
        <ul className="nav-left">
          <li><Link to="/"></Link></li>
          <li><Link to="/about"> </Link></li>
          <li><Link to="/services"></Link></li>
        </ul>
        <div className="header-logo">Beauty Laboratorium</div>
        <ul className="nav-right">
          <li><Link to="/labelage"> Labelage</Link></li>
          <li><a href="#contacts" onClick={e => handleNavClick(e, 'contacts')}></a></li>
          <li><a href="#reviews" onClick={e => handleNavClick(e, 'reviews')}></a></li>
        </ul>
      </nav>
      <div className="header-divider" />
    </header>
  );
}

export default Header; 