export default function ProductModal({ onClose, product = 1 }) {
  const data = productData[product];
  return (
    <div className="product-modal-overlay" onClick={onClose}>
      <div className="product-modal" onClick={e => e.stopPropagation()}>
        <button className="product-modal-close" onClick={onClose}>&times;</button>
        <div className="product-modal-content">
          <div className="product-modal-row">
            <div className="product-modal-text">
              <p>{data.textTop}</p>
            </div>
            <div className="product-modal-image-block">
              <img src={data.img} alt={data.title} className="product-modal-image" />
              <div className="product-modal-title">{data.title}</div>
            </div>
          </div>
          <div className="product-modal-text product-modal-text-full">
            <p>{data.textBottom}</p>
          </div>
        </div>
      </div>
    </div>
  );
} 